


.wrapper {
	padding: 4px 0px 4px 16px;
	.item {
		display: flex;
		flex-direction: column;
		.main {
			padding: 8px 16px;
			display: flex;
			align-items: center;
			background: var(--background-sec);
			cursor: pointer;
			transition: background 250ms linear, color 250ms linear;
			border-radius: 3px;
			font-size: 1.25rem;
			&:not(:last-child) {
				margin-bottom: 4px;
				border-bottom: 1px solid rgb(0 0 0 / 12%);
			}
			&:hover {
				background-color: var(--border-color);
				color: var(--white);
			}
		}
		.title {
			font-size: 0.95rem;
		}
		.item_icon {
			margin-right: 16px;
		}
	}
}
